@use '../module' as *;

.about-me {
	background: $second-bck;
	padding-bottom: 3em;
	min-height: 100vh;

	.title {
		font-size: 1.8rem;
		font-weight: 800;
		margin: 0;
		padding: 1em 0 0;
		letter-spacing: 3px;
		text-align: center;

		@include breakpoint(tablet) {
			font-size: 2rem;
		}

		@include breakpoint(laptop) {
			font-size: 2.3rem;
		}

		@include breakpoint(mine) {
			font-size: 3rem;
		}
	}

	&__container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-evenly;
		padding: 0 2em;

		@include breakpoint(tablet) {
			flex-direction: column;
			align-items: center;
		}
		@include breakpoint(laptop) {
			flex-direction: row;
		}

		.img-box {
			border-radius: 70%;
			overflow: hidden;

			margin: 3em 0;

			height: 210px;
			width: 216px;

			@include breakpoint(mine) {
				width: 268px;
				height: 263px;
			}

			img {
				margin-bottom: -10px;
				width: 100%;
			}

			.features__info {
				position: absolute;
				padding: 0.7em 0.7em;
				// margin-top: -3em;
				margin-left: 0.6em;
				max-width: 178px;

				background: $features-bck;
				border-radius: 2.6875em;

				text-align: center;
				line-height: 1.6;
				font-size: 1rem;
				font-weight: $fw-n;

				@include breakpoint(mine) {
					max-width: 214px;
					font-size: 1.2rem;
				}

				@include breakpoint(large-l) {
					font-size: 1.3rem;
					max-width: 231px;
					margin-left: 0.3em;
				}

				i {
					margin-right: 0.4em;
					color: $dog-rose;
					opacity: 0.7;
				}
			}
		}

		.content {
			margin: 3.7em 0 0;
			font-size: 1rem;
			color: $body;
			font-weight: $fw-m;
			letter-spacing: 2px;
			line-height: 1.4;

			@include breakpoint(tablet) {
				max-width: 87%;
				font-size: 1.1rem;
			}

			@include breakpoint(laptop) {
				font-size: 1.1rem;
				max-width: 51%;
			}

			@include breakpoint(mine) {
				margin-top: 2.4em;
				font-size: 1.3rem;
				line-height: 1.3;
			}

			@include breakpoint(large-l) {
				font-size: 1.45rem;
			}
		}
	}
}
